      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-primitive-ground</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-primitive</span> <span class="hljs-attr">:appearance</span>=<span class="hljs-string">"appearance"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-instance-geometry</span> <span class="hljs-attr">:attributes</span>=<span class="hljs-string">"attributes"</span>&gt;</span>
