            <span class="hljs-attr">ref</span>=<span class="hljs-string">"ellipseOutline"</span>
            <span class="hljs-attr">:center</span>=<span class="hljs-string">"center"</span>
            <span class="hljs-attr">:semiMinorAxis</span>=<span class="hljs-string">"200000.0"</span>
            <span class="hljs-attr">:semiMajorAxis</span>=<span class="hljs-string">"300000.0"</span>
            <span class="hljs-attr">:height</span>=<span class="hljs-string">"100000"</span>
          &gt;</span>
          <span class="hljs-tag">&lt;/<span class="hljs-name">vc-geometry-outline-ellipse</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">vc-instance-geometry</span>&gt;</span>
