<h2>属性</h2>
<h3><code v-pre>vc-geometry-ellipse</code></h3>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>center</td>
<td>Object</td>
<td></td>
<td><code v-pre>required</code> 指定椭圆的中心位置。 <strong>结构: { lng: number, lat: number, height: number }</strong></td>
</tr>
<tr>
<td>semiMajorAxis</td>
<td>Number</td>
<td></td>
<td><code v-pre>required</code> 指定椭圆的长半轴长度，单位是米。</td>
</tr>
<tr>
<td>semiMinorAxis</td>
<td>Number</td>
<td></td>
<td><code v-pre>required</code> 指定椭圆的短半轴长度，单位是米。</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定椭圆的参考椭球体。</td>
</tr>
<tr>
<td>height</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> 指定椭圆离地表的高度。</td>
</tr>
<tr>
<td>extrudedHeight</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定椭圆拉伸高度。</td>
</tr>
<tr>
<td>rotation</td>
<td>Number</td>
<td><code v-pre>0.0</code></td>
<td><code v-pre>optional</code> 指定椭圆以正北逆时针方向旋转的角度。</td>
</tr>
<tr>
<td>stRotation</td>
<td>Number|Object</td>
<td><code v-pre>0.0</code></td>
<td><code v-pre>optional</code> 指定椭圆纹理以正北逆时针方向旋转的角度。</td>
</tr>
<tr>
<td>granularity</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定椭圆上点之间的角距离（弧度）。</td>
</tr>
<tr>
<td>vertexFormat</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定顶点属性渲染方式。</td>
</tr>
</tbody>
</table>
<hr>
<h3><code v-pre>vc-geometry-outline-ellipse</code></h3>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>center</td>
<td>Object</td>
<td></td>
<td><code v-pre>required</code> 指定椭圆的中心位置。 <strong>结构: { lng: number, lat: number, height: number }</strong></td>
</tr>
<tr>
<td>semiMajorAxis</td>
<td>Number</td>
<td></td>
<td><code v-pre>required</code> 指定椭圆的长半轴长度，单位是米。</td>
</tr>
<tr>
<td>semiMinorAxis</td>
<td>Number</td>
<td></td>
<td><code v-pre>required</code> 指定椭圆的短半轴长度，单位是米。</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定椭圆的参考椭球体。</td>
</tr>
<tr>
<td>height</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> 指定椭圆离地表的高度。</td>
</tr>
<tr>
<td>extrudedHeight</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定椭圆拉伸高度。</td>
</tr>
<tr>
<td>rotation</td>
<td>Number</td>
<td><code v-pre>0.0</code></td>
<td><code v-pre>optional</code> 指定椭圆以正北逆时针方向旋转的角度。</td>
</tr>
<tr>
<td>stRotation</td>
<td>Number|Object</td>
<td><code v-pre>0.0</code></td>
<td><code v-pre>optional</code> 指定椭圆纹理以正北逆时针方向旋转的角度。</td>
</tr>
<tr>
<td>granularity</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定椭圆上点之间的角距离（弧度）。</td>
</tr>
<tr>
<td>numberOfVerticalLines</td>
<td>Number</td>
<td><code v-pre>16</code></td>
<td><code v-pre>optional</code> 指定拉伸的椭圆连接顶部与底部的线条数量。</td>
</tr>
</tbody>
</table>
<p>参考官方文档: <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/EllipseGeometry.html">EllipseGeometry</a></strong>、 <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/EllipseOutlineGeometry.html">EllipseOutlineGeometry</a></strong></p>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    export default {
      data() {
        return {
          appearance: null,
          geometry: null,
          attributes: null,
          center: { lng: 102, lat: 38 }
        }
      },
      mounted() {
        Promise.all([this.$refs.ellipse.createPromise, this.$refs.ellipseOutline.createPromise]).then((instances) => {
          console.log('All geometries are loaded.')
          const boundingSphereUnion = instances.reduce((prev, cur) => {
            const geometry = cur.cesiumObject.constructor.createGeometry(cur.cesiumObject)
            const boundingSphere = cur.vm.$parent.modelMatrix
              ? Cesium.BoundingSphere.transform(geometry.boundingSphere, cur.vm.$parent.modelMatrix)
              : geometry.boundingSphere
            return prev === null ? boundingSphere : Cesium.BoundingSphere.union(prev, boundingSphere)
          }, null)
          instances[0].viewer.scene.camera.flyToBoundingSphere(boundingSphereUnion)
        })
      },
      methods: {
        ready({ Cesium, viewer }) {
          this.viewer = viewer
          const { PerInstanceColorAppearance, ColorGeometryInstanceAttribute } = Cesium
          this.appearance = new PerInstanceColorAppearance({
            flat : true
          })
          this.attributes = {
            color: new ColorGeometryInstanceAttribute(Math.random(), Math.random(), Math.random(), 0.5)
          }
        },
        LEFT_CLICK(movement) {
          const feature = this.viewer.scene.pick(movement.position)
          console.log(feature)
        }
      }
    }
  </script>